<template>
  <div id="about">
    <p id='detail'><a>{{ name }}</a></p>
    <p><font-awesome-icon icon="envelope" /> <a class='underLine'>{{ mail_2 }}</a></p>
    <p><font-awesome-icon icon="gift" /> <a class='underLine'>{{ gitAccount }}</a></p>
    <hr>
  </div>
</template>
<script>
import { store } from '../store/store'
export default {
  store: store,
  name: 'BlogAbout',
  data () {
    return {
      msg: ' ---- ',
      name: '马骁博',
      mail_1: 'maixaobo2015@gmail.com',
      mail_2: 'maixaobo2015@163.com',
      carrer: '前端攻城狮~汪汪',
      gitAccount: 'https://github.com/MichaelMa666'
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.$store.state.moduleAbout.domHeight = document.querySelectorAll('#about')[0].clientHeight
    })
  },
  methods: {
  }
}
</script>
<style lang='less' scoped>
@import '../less/varables.less';
#about {
  text-align: left;
  margin-left: 2em;

}
.underLine {
  text-decoration-line: underline;
  cursor: pointer;
}
@media screen and (max-device-width: 600px){
  #about {
    text-align: left;
    margin:0 auto;
    width: 80vw;
  }
}
#about svg {
  color: #42b983;
}
#detail {
  position: relative;
  top: 10px;
}
</style>



